import {platformBrowserDynamic} from '@angular/platform-browser-dynamic';
import {enableProdMode} from '@angular/core';

import {AppModule} from './app/app.module';

try {
    console.assert(process, 'process is undefined.');
    console.assert(process.env, 'process.env is undefined.');
    console.assert(process.env.ENV, 'process.env.ENV is undefined.');

    console.info("Current Mode: " + process.env.ENV);

    if (process.env.ENV === 'production') {
        enableProdMode();
    }
} catch (e) {
    console.error(e);
}

platformBrowserDynamic().bootstrapModule(AppModule).catch((error) => {
    console.error('app error: %o', error);
});



